import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
//import SpeciesItem from './SpeciesItem';
import './SpeciesCont.scss'
import RouteContext from '../context/RouteProvider';
import LocContext from '../context/LocProvider';
import getSpeciesData from '../utils/getSpeciesData';


function SpeciesCont(props) {
  const [speciesData, speciesDataSet] = useState(null)

  var query = "https://us-central1-species-eebec.cloudfunctions.net/app"
  const loc = useContext(RouteContext).match.params.lugar
  const onLocChange = useContext(LocContext).locInfo.onLocChange
  
  useEffect(()=>{
    if(loc){
      onLocChange(loc)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  },[loc])

  if(loc){
    query += `?lugar=${loc}`
    console.log("location: " + loc)
  } else {
    query += `?lugar=Chajarí`
    console.log("ERROR - SpeciesCont: No loc name")
  }
  const getSpecies = async () => {
    console.log(query)
    await axios
      .get(query)
      .then((response) => {
        const dataResp = response.data
        speciesDataSet(JSON.parse(dataResp))
      })
      .catch((error) => {
        console.log("ERROR!!! : ", error)
        //speciesDataSet(JSON.parse(testData))
      })
  }
  
  useEffect(async () => {
    await getSpecies()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  //const speciesItemList = []
  const speciesArr = []
  if(speciesData){
    console.log(speciesData)
    for(let i = 0; i < speciesData.species.length || i < 20; i++){
      speciesArr.push(speciesData.species[i])
      /*
      speciesItemList.push(
        <SpeciesItem species={speciesData.species[i]}/>
      )
      */
    }
    getSpeciesData(speciesArr).then(

    )
  }
  
  return (
    <div className="speciesCont">
      {/*speciesItemList*/}
    </div>
  )
}

export default SpeciesCont;